import React from 'react';
import ErrorBoundary from './Error';

// import styles from './Lesson5.module.css'

function Lesson5() {
  return (
    <div className="App">
      <h1>Modulul 3 - Lecia 5 - Lifecycle</h1>
      <h2>Lifecycle</h2>
      <p>
        Exist mai multe etape de via pentru o component -{' '}
        <b>mounting, updating i unmounting</b>. n timpul fiecreia, metodele
        motenite de la React.Component sunt apelate pe clasa componentei. Putem
        modifica comportamentul acestora pentru fiecare etap, adugnd
        funcionalitatea necesar, n cadrul unor reguli stabilite.
      </p>
      <p>
        Exist n total apte metode pentru ciclul de via a unei componente,
        fr a lua n considerare render i constructor. n practic, pentru
        majoritatea sarcinilor de zi cu zi se folosesc doar trei:
        <b>componentDidMount, componentDidUpdate i componentWillUnmount</b>.
      </p>
      <a href="https://projects.wojtekmaj.pl/react-lifecycle-methods-diagram/">
        Diagrama functionare React
      </a>
      <h3>Etapa de mounting</h3>
      <p>
        Urmtoarele metode sunt apelate n aceast ordine, atunci cnd
        componenta este instaniat i adugat la DOM.
      </p>
      <p
        style={{
          color: 'red',
        }}
      >
        <b>constructor() &#123;&#125;</b>
      </p>
      <p>
        Se apeleaz atunci cnd componenta este instaniat, nainte s fie
        plasat n DOM.
      </p>
      <ul>
        <li>Iniializeaz starea iniial a componentei</li>
        <li>Adaug contextul n metode</li>
        <li>Nu se poate apela aici setState()</li>
        <li>
          n cele mai multe cazuri, specificarea explicit a unui constructor
          este exagerat
        </li>
      </ul>
      <p
        style={{
          color: 'red',
        }}
      >
        <b>
          static getDerivedStateFromProps(nextProps, prevState) &#123;&#125;
        </b>
      </p>
      <ul>
        <li>Aceast metod este rar folosit n practic.</li>
        <li>
          Se apeleaz nainte de render(), la mounting i nainte de toate
          apelurile render, adic dup actualizarea de state sau props
        </li>
        <li>
          Poate fi folosit pentru a seta state, n funcie de props , de fiecare
          dat cnd acestea se schimb
        </li>
        <li>
          Ar trebui s returneze un obiect cu care s actualizeze starea sau
          null dac nu trebuie actualizat nimic
        </li>
        <li>Nu are acces la contextul this.</li>
      </ul>
      <p
        style={{
          color: 'red',
        }}
      >
        <b>render() &#123;&#125;</b>
      </p>
      <ul>
        <li>Permite descrierea declarativ a unei interfee</li>
        <li>Returneaz rezultatul expresiilor JSX, subarborele Virtual DOM</li>
        <li>Nu se poate apela aici setState()</li>
      </ul>

      <p
        style={{
          color: 'red',
        }}
      >
        <b>componentDidMount() &#123;&#125;</b>
      </p>
      <ul>
        <li>Este apelat imediat dup ce componenta face mounting n DOM</li>
        <li>
          Aici se efectueaz solicitri HTTP, se aga custom listeners pe
          evenimente i se efectueaz operaii pe arborele DOM
        </li>
        <li>
          Apelul de setState() va provoca un re-render n aceast metod - acest
          lucru este perfect normal
        </li>
      </ul>

      <h3>Etapa de actualizare</h3>
      <p>
        O actualizare poate fi declanat de o schimbare n state a componentei
        n sine sau de datele modificate din props transmise acesteia. La
        actualizare, componenta trebuie redat din nou, ceea ce duce la apelarea
        urmtoarelor metode.
      </p>

      <p
        style={{
          color: 'red',
        }}
      >
        <b>shouldComponentUpdate(nextProps, nextState) &#123;&#125;</b>
      </p>
      <ul>
        <li>Nu este apelat cnd componenta se iniializeaz</li>
        <li>
          Este apelat nainte de a face re-render la o component deja
          "mounted"
        </li>
        <li>Este necesar exclusiv pentru optimizarea procesului de randare</li>
        <li>
          n mod implicit, render se execut atunci cnd sunt modificri n
          props sau state
        </li>
        <li>
          Permite compararea vechiului i noului state i props , returnnd true
          sau false, iar pe baza acestui rezultat React tie dac componenta
          trebuie actualizat
        </li>
        <li>
          Dac returneaz false, atunci render() i componentDidUpdate() nu vor
          avea loc
        </li>
        <li>Nu se poate apela setState()</li>
        <li>
          Trebuie folosit cu mare atenie, numai dac ajut la performan.
          Astfel, poate duce la un efect opus
        </li>
        <li>
          Ar putea fi nlocuit cu <b>React.PureComponent</b> care va face o
          comparaie superficial a datelor din props. Doar dac ajut la
          performan.
        </li>
      </ul>

      <p
        style={{
          color: 'red',
        }}
      >
        <b>getSnapshotBeforeUpdate(prevProps, prevState) &#123;&#125;</b>
      </p>

      <ul>
        <li>Aceast metod este rar folosit n practic.</li>
        <li>
          Este apelat nainte ca modificrile care sunt gata s fie adugate la
          DOM
        </li>
        <li>
          Poate fi folosit pentru a obine valori DOM pre-actualizare, cum ar fi
          poziia curent a scroll-bar-ului sau dimensiunea elementului nainte
          de actualizare
        </li>
        <li>
          Ceea ce returneaz aceast metod va fi transmis ca al treilea
          parametru snapshot n componentDidUpdate()
        </li>
      </ul>

      <p
        style={{
          color: 'red',
        }}
      >
        <b>componentDidUpdate(prevProps, prevState, snapshot) &#123;&#125;</b>
      </p>

      <ul>
        <li>Este apelat imediat dup actualizarea componenei n DOM</li>
        <li>Nu este apelat cnd componenta execut primul render</li>
        <li>
          Se poate apela setState(), ns trebuie s ne asigurm neaprat c l
          includem ntr-o condiie pentru a verifica dac datele din props sau
          state s-au modificat, astfel nct s nu existe un ciclu nesfrit de
          rendering (tab-ul va nghea sau se va nchide).
        </li>
        <li>Se pot face cereri HTTP</li>
        <li>
          Dac componenta are <b>getSnapshotBeforeUpdate()</b>, atunci valoarea
          returnat de aceasta va fi transmis ca al treilea argument snapshot.
          n caz contrar, valoarea sa va fi undefined
        </li>
      </ul>

      <h3>Etapa de unmounting</h3>
      <p>
        La un moment dat, componenta va fi eliminat din DOM. Aici se va apela
        urmtoarea metod.
      </p>

      <p
        style={{
          color: 'red',
        }}
      >
        <b>componentWillUnmount() &#123;&#125;</b>
      </p>

      <ul>
        <li>
          Este apelat nainte ca elementul s fie unmounted (eliminat) i scos
          din DOM
        </li>
        <li>
          Este excelent pentru a cura anumite elemente: listeners,
          temporizatoare, solicitri HTTP. n caz contrar, vor exista scurgeri
          de memorie
        </li>
        <li>
          Aici nu are sens apelul lui setState(), deoarece componenta nu va face
          re-render
        </li>
      </ul>

      <h3>Gestionarea erorilor de rendering</h3>
      <p>
        React iubete s defineasc comportamentul ntregii aplicaii n cazul
        unei erori. Metoda <b>componentDidCatch</b> se declaneaz atunci cnd
        apare o eroare ntr-o component copil. Astfel, permite componentei
        printe s detecteze erori la copii i s afieze o interfa de
        rezerv. Prin urmare, atunci cnd apare o eroare, interfaa de UI nu se
        blocheaz.
      </p>

      <p
        style={{
          color: 'red',
        }}
      >
        <b>componentDidCatch(error, info) &#123;&#125;</b>
      </p>

      <ul>
        <li>Se folosete pentru controlul erorilor</li>
        <li>Capteaz erori doar la copii, nu i la componenta printe</li>
        <li>
          <b>error</b> - rezultatul <b>toString()</b> pentru obiectul de eroare
        </li>
        <li>
          <b>info</b> - obiect ce descrie <b>stack trace</b>
        </li>
      </ul>

      <ErrorBoundary />

      <h3>Materiale adiionale</h3>
      <ul>
        <li>
          <a href="https://legacy.reactjs.org/docs/react-component.html#the-component-lifecycle">
            Documentation - The component lifecycle
          </a>
        </li>
        <li>
          <a href="https://legacy.reactjs.org/blog/2017/07/26/error-handling-in-react-16.html">
            Error Handling in React 16+
          </a>
        </li>
      </ul>
    </div>
  );
}

export default Lesson5;
