import React from 'react';
// import { Component } from 'react';

class ErrorBoundary extends React.Component {
  state = { hasError: true };

  componentDidCatch(error, info) {
    // Dac metoda a fost apelat, atunci exist o eroare!
    // Stabilim starea componentei
    this.setState({ hasError: true });
    // De asemenea, putem trimite un raport de eroare serviciului de analiz
    // logErrorToMyService(error, info);
  }

  render() {
    // Dac exist o eroare...
    if (this.state.hasError) {
      // Vom randa fallback UI
      return <h1>Something went wrong, please try again later :(</h1>;
    }

    // Dac totul este n regul, vom randa componentele copii
    return this.props.children;
  }
}

export default ErrorBoundary;
