import ClassMethods from './ClassMethods';
import BindRender from './BindRender';
import BindConstructor from './BindConstructor';
import PublicClassProps from './PublicClassProps';
import InternalState from './InternalState';
import InitialState from './InitialState';
import InitialStateSimplifyed from './InitialStateSimplifyed';
import Toggle from './Toggle';
import ToggleState from './ToggleState';
import CounterFinal from './CounterFinal';
import StateAscension from './StateAscension';

import image1 from './images/image1.png';
import image2 from './images/image2.png';
import image3 from './images/image3.png';

import styles from './Lesson3.module.css';

function Lesson3() {
  return (
    <div className="App">
      <h1>Modulul 2 - Lecia 3 - Events i state</h1>

      <h2>Componente class based</h2>
      <p>
        Dac trebuie s adugam dinamic, atunci componentele sunt create ca
        clase, deoarece componentele funcionale (nainte de hooks) sunt
        limitate la capacitatea de a crea markup doar pe baza datelor din props
        primite.
      </p>
      <img className={styles.img} src={image1} alt="example" />
      <ul>
        <li>
          O clas ES6 obinuit, deci se aplic toate regulile: constructor,
          metode, context (this).
        </li>
        <li>Obligatoriu extinde clasa de baz React.Component. </li>
        <li>
          Acioneaz ca o funcie care primete props, dar i implementeaz o
          stare intern privat.
        </li>
        <li>
          Trebuie declarat o metod obligatorie render() care este apelat
          implicit i returneaz un markup JSX
        </li>
        <li>
          De fiecare dat cnd folosim o component class based, React va crea o
          instan a componentei (clasei), de aceea datele sunt accesate prin
          this.props.
        </li>
        <li>
          Putem defini metode de clas personalizate i le putem utiliza
          oriunde, inclusiv n interiorul JSX, le putem apela sau transmite
          copiilor ca props.
        </li>
        <li>
          Cnd starea sau datele din props ale unei componente se modific,
          aceasta este re-randat (re-render).
        </li>
      </ul>
      <p
        style={{
          color: 'red',
        }}
      >
        <b>
          import React, &#123; Component &#125; from "react"; <br /> class
          MyClassComponent extends Component &#123; <br /> static defaultProps =
          &#123; &#125; ;
          <br />
          static propTypes = &#123;&#125;;
          <br />
          render() &#123;
          <br />
          return &lt;div&gt;Class Component&lt;/div&gt;;
          <br />
          &#125; &#125; <br />
        </b>
      </p>

      <h2>Events</h2>
      <p>
        Pentru un eveniment nativ din browser, React creeaz un obiect wrapper
        <b>SyntheticEvent</b> Object cu o interfa identic. Acest lucru este
        necesar pentru a oferi cross-browser compatibility i pentru a optimiza
        performana.
      </p>

      <p>
        <b>Uita-te in consola !!!</b>
      </p>

      <button
        style={{
          background: 'red',
          color: 'white',
          padding: '30px',
          fontSize: '20px',
        }}
        onClick={event => console.log(event)}
      >
        Click me!
      </button>

      <ul>
        <li>
          Adugarea unui handler de evenimente cu EventTarget.addEventListener()
          nu este folosit aproape niciodat, excepiile fiind rare.
        </li>
        <li>
          Props events nu sunt o excepie i vor fi denumite folosind camelCase.
          De exemplu: onClick, onChange, onSubmit, onMouseEnter.
        </li>
        <li>
          n props se transmite o referin la funcia callback care va fi
          apelat atunci cnd are loc evenimentul.
        </li>
        <li>Events Handler primesc o instan a SyntheticEvent Object.</li>
      </ul>
      <p>
        n spate, React implementeaz, de fapt, delegarea evenimentului. Event
        Listeners nu sunt adugai direct pe elementele din DOM. Transmiterea
        unui callback nseamn pur i simplu nregistrarea unei funcii care va
        fi apelat de un mecanism intern din React atunci cnd are loc un
        eveniment.
      </p>

      <h3>Counter</h3>
      <p>
        S crem o component de contor cu capacitatea de a mri i micora
        valoarea sa.
      </p>

      <Counter step={5} />

      <h3>Anonymous callback function</h3>
      <p>
        Inline callbacks sunt considerate anti-pattern. De fiecare dat cnd
        componenta face re-render, va fi creat o nou funcie callback. n
        multe cazuri, acest lucru este normal. Dar, dac callback-ul este
        transmis ca props la componentele subiacente din arbore, acestea vor
        face i ele re-render, deoarece sosesc date noi n props de tip
        referin (funcie). n plus, funciile inline cu o logic mare n JSX
        interfereaz cu lizibilitatea codului de markup a componentei.
      </p>

      <Anonymous step={5} />

      <h3>Metodele clasei</h3>
      <p>
        Cel mai adesea, handlerii de evenimente sunt declarai ca metode de
        clas, dup care o referin la metod este transmis atributului jsx.
      </p>

      <ClassMethods step={5} />

      <h3>Legarea contextului</h3>
      <p>
        Ar trebui s inem ntotdeauna cont de semnificaia lui this n metodele
        utilizate ca callback functions. n JavaScript, contextul n metodele de
        clas nu este adugat n mod implicit. Dac uitai s legai contextul
        i s transmitei metoda ca funcie callback la handler-ul de
        evenimente, n timpul apelului funciei, this va fi nedefinit
        (undefined).
      </p>

      <h4>Legarea contextului la transmiterea funciei callback</h4>
      <p>
        Evitai legarea contextului n metoda render(). Ori de cte ori o
        component face re-render, Function.prototype.bind() returneaz o nou
        funcie i o transmite n jos peste toate componentele arborelui,
        determinnd re-randarea componentelor copil. Atunci cnd sunt multe
        componente, acest lucru are un impact semnificativ asupra performanei.
      </p>

      <BindRender step={5} />

      <h4>Legarea contextului de constructor</h4>
      <p>
        O alt modalitate de a lega un context este n constructorul clasei.
        Dac exist o mulime de funcii callback, v putei imagina ct de mare
        poate s devin constructorul.
      </p>
      <ul>
        <li>
          Constructorul este executat o dat, deci bind va fi apelat o dat
        </li>
        <li>
          Metodele de clas sunt scrise n proprietatea prototype a
          funciei-constructor
        </li>
      </ul>

      <BindConstructor step={5} />

      <h4>Proprietile publice dintr-o clas</h4>
      <p>
        Dei aceasta este modalitatea recomandat de a lega un context, sintaxa
        pentru metodele publice nu a fost nc standardizat. Dar ele sunt deja
        att de utilizate nct, chiar dac exist modificri sintactice,
        transpilerul Babel va face totul pentru noi.
      </p>

      <PublicClassProps step={5} />

      <h3>Materiale suplimentare</h3>
      <a href="https://legacy.reactjs.org/docs/events.html">
        Documentaie pentru SyntheticEvent
      </a>

      <h2>Starea intern a componentei</h2>
      <p>
        Obiectul <b>state</b> este o proprietate de clas care nu trebuie
        modificat direct de ctre programator.
      </p>
      <ul>
        <li>
          Datele din <b>state</b> controleaz ceea ce este afiat n interfa.
        </li>
        <li>
          Datele stocate n <b>state</b> ar trebui s fie informaii care vor fi
          actualizate prin metodele componentei.
        </li>
        <li>
          Nu este nevoie ca datele s fie duplicate din <b>props</b> n{' '}
          <b>state</b>.
        </li>
        <li>
          De fiecare dat cnd se modific starea componentei (sau props), se
          apeleaz metoda render().
        </li>
      </ul>
      <p>
        State-ul stocheaz setul minim necesar de date pe baza cruia putem
        calcula tot ce e nevoie pentru a desena interfaa. Acest lucru se face
        prin apelarea selectoarelor (funcii care compun datele pentru
        interfaa, bazat pe stare) n metoda render(). Aa obinem datele
        calculate.
      </p>
      <img className={styles.img} src={image2} alt="Example" />
      <ul>
        <li>Interfaa depinde de starea componentei.</li>
        <li>
          State-ul se poate schimba ca rspuns la aciunile utilizatorului.
        </li>
        <li>
          Cnd state-ul se schimb, datele sunt transmise n jos pe toate
          componentele arborelui.
        </li>
        <li>
          Componentele returneaz un markup actualizat i modificri ale
          interfeei.
        </li>
      </ul>
      <p>
        State-ul aparine componentei i este schimbat doar prin metodele sale
        interne. Schimbarea strii unei componente nu va afecta niciodat
        printele, vecinii sau orice alt component din aplicaie, ci doar pe
        copiii acesteia. Cu acest model, datele din aplicaie sunt transmise
        ntr-un singur mod, strict limitat. Acesta se numete flux de date
        unidirecional.
      </p>
      <img className={styles.img} src={image3} alt="Example" />
      <p>
        Starea este declarat n constructor, deoarece este primul lucru care se
        ntmpl atunci cnd clasa este instaniat.
      </p>

      <InternalState step={5} value={7} />

      <h3>Initial state based on props</h3>
      <p>
        Uneori, starea iniial depinde de date primite din props, cum ar fi
        valoarea iniial a contorului nostru. n acest caz, trebuie declarat n
        mod explicit parametrul props n constructor i trebuie transmis
        apelului super(props). Apoi, this.props va fi disponibil n constructor.
      </p>

      <InitialState step={5} value={7} initialValue={3} />

      <p>
        Deoarece n spate este folosit Babel, putem sri peste declarrile
        plictisitoare ale constructorului i s specificm state-ul ca
        proprietate public a clasei, iar transpilerul se va ocupa de restul
        pentru noi.
      </p>

      <InitialStateSimplifyed step={5} initialValue={3} />

      <h3>Modificarea state-ului unei componente</h3>
      <p>
        Pentru a actualiza starea unei componente este utilizat metoda
        ncorporat, <b>setState()</b>.
      </p>
      <ul>
        <li>
          Primul argument obligatoriu este un obiect cu cmpuri care indic ce
          parte a strii trebuie schimbat.
        </li>
        <li>
          Al doilea argument este opional - putem trece o funcie callback care
          va fi executat dup schimbarea strii.
        </li>
      </ul>
      <p style={{ color: 'red' }}>
        <b>PERICOL</b>
      </p>
      <p>
        Nu putei schimba starea direct prin referin. Fii foarte ateni, mai
        ales cnd lucrai cu tipuri de referin (matrice, obiect).
      </p>
      <p>
        state = &#123; fullName: "Poly" &#125;;
        <br />
        <br />  Bad <br /> this.state.fullName = "Mango";
        <br />
        <br />  Good <br />
        this.setState(&#123; fullName: "Mango", &#125;);
      </p>
      <p>
        Aceast abordare este utilizat atunci cnd noua stare nu este calculat
        pe baza celei anterioare. Adic, atunci cnd ceva nou se scrie n state,
        suprascriindu-l pe cel existent. S facem o component cu un comutator
        ale crui metode vor suprascrie valoarea lui isOpen n state.
      </p>

      <Toggle>Vizibil</Toggle>

      <h3>Cum se actualizeaz state-ul?</h3>
      <p>
        La apelul lui setState(), nu trebuie s trecem toate proprietile
        stocate n state. Este suficient s specificam doar acea parte (slice) a
        strii pe care dorim s o modificm n aceast operaie. React preia,
        apoi, starea curent i obiectul care a fost transmis la setState(),
        concatenndu-le astfel:
      </p>
      <p
        style={{
          color: 'red',
        }}
      >
        <b>
          state-ul nainte de fuzionare <br />
          const currentState = &#123; a: 2, b: 3, c: 7, d: 9 &#125;; <br />
          <br />
          obiectul transmis n setState <br />
          const updateSlice = &#123; b: 5, d: 4 &#125;; <br />
          <br />
          noua valoare lui this.state dupa concatenare <br />
          const nextState = &#123; ...currentState, ...updateSlice &#125;;{' '}
          <br /> &#123;a: 2, b: 5, c: 7, d: 4&#125;
        </b>
      </p>

      <h3>Actualizare asincron a strii</h3>
      <p>
        Metoda setState() nregistreaz o operaie asincron de actualizare a
        strii 4 care este pus ntr-o coad. React nu schimb starea pentru
        fiecare apel al funciei setState(), dar poate combina mai multe apeluri
        ntr-o singur actualizare pentru a mbunti performana. Din acest
        motiv, accesnd this.state n cod sincron dup apelarea acestei metode,
        va fi returnat valoarea de dinaintea actualizrii.
      </p>
      <p>
        S ne imaginm c atunci cnd modificm starea, ne bazm pe valoarea
        actual a acesteia pentru a o calcula urmtoarea. Folosim o bucl for
        pentru a crea (nregistra) mai multe actualizri.
      </p>
      <p style={{ color: 'red' }}>
        <b>
          S presupunem c avem o astfel de stare <br />
          state = &#123; value: 0 &#125;; <br />
          <br />
          Executm o bucl i crem 3 operaii de actualizare <br />
          for (let i = 0; i &lt; 3; i += 1) &#123; <br />
          Dac ne uitm la state, la toate iteraiile va fi 0 <br />
          Deoarece acesta este un cod sincron i actualizarea de stare nu a avut
          loc nc <br />
          console.log(this.state.value); <br />
          <br />
          this.setState(&#123; value: this.state.value + 1 &#125;); <br />
          &#125;
        </b>
      </p>
      <p>
        Valoarea proprietii this.state.value este reinut n momentul n care
        obiectul transmis ctre setState() este creat, nu n momentul
        actualizrii strii. Adic, dac n momentul crerii obiectului,
        this.state.value coninea 0, obiectul &#123;value: 0 + 1&#125; este
        transmis funciei setState().
      </p>
      <p>
        Ca rezultat al buclei, obinem o coad de 3 obiecte &#123; value: 0 + 1
        &#125;, &#123; value: 0 + 1 &#125;, &#123; value: 0 + 1 &#125; i starea
        iniial la momentul actualizrii &#123; value: 0 &#125;. Dup toate
        actualizrile, obinem starea &#123; value: 1 &#125;.
      </p>
      <p>
        Prin urmare, nu ne putem baza pe starea curent atunci cnd o calculm
        pe urmtoarea care depinde de cea anterioar la momentul actualizrii.
        Acest lucru poate duce la erori. Prin urmare, exist o a doua modalitate
        de a actualiza starea.
      </p>

      <h3>setState cu o funcie</h3>
      <p style={{ color: 'red' }}>
        <b>
          S presupunem c exist o astfel de stare <br />
          state = &#123; value: 0 &#125;; <br />
          <br />
          Executm o bucl i crem 3 operaii de actualizare <br />
          for (let i = 0; i &lt; 3; i += 1) &#123; <br />
          Dac ne uitm la state, la toate iteraiile va fi 0 <br />
          Deoarece acesta este un cod sincron i actualizarea de stare nu a avut
          loc nc <br />
          console.log(this.state.value); este 0 <br />
          <br />
          this.setState(prevState =&gt; &#123; Dac ne uitm la starea transmis
          funciei callback n timpul apelului acesteia, <br />
          vom obine starea curent la momentul actualizrii. <br />
          console.log(prevState.value); va fi diferit la fiecare iteraie <br />
          <br />
          return &#123; value: prevState.value + 1 &#125;; <br />
          &#125;); <br />
          &#125; <br />
        </b>
      </p>
      <p>
        De fiecare dat cnd funcia transmis la setState() este apelat, o
        referin la starea curent la momentul actualizrii va fi transmis
        parametrului prevState. Vom obine obiectele actualizate &#123;value: 0
        + 1&#125;, &#123;value: 1 + 1&#125;, &#123;value: 2 + 1&#125; i, ca
        rezultat, this.state.value va fi 3 .
      </p>
      <p>
        Acum putem nlocui funcionalitatea de deschidere/nchidere din
        componenta &lt;Toggle&gt;.
      </p>

      <ToggleState>Visible</ToggleState>

      <p>Iar contorul va arta astfel.</p>

      <CounterFinal step={5} initialValue={3} />

      <h3>Ascensiunea state-ului</h3>
      <p>
        Deoarece React utilizeaz un flux de date unidirecional de sus n jos,
        pentru a schimba starea printelui la un eveniment din copil, este
        utilizat urmtorul pattern cu o funcie callback.
      </p>
      <img
        src="https://textbook.edu.goit.global/react-zr7b4k/v1/ro/img/lesson-03/state-hoisting.gif"
        alt="Gif"
      />
      <ul>
        <li>Printele are un state i o metod care o poate schimba.</li>
        <li>
          Copilului, sub form de props, i se transmite metoda printelui care
          schimb starea acestuia.
        </li>
        <li>n componenta copil se apeleaz metoda transmis.</li>
        <li>
          Cnd aceast metod este apelat, starea printelui este schimbat.
        </li>
        <li>Se ntmpl re-render la toate componentele de sub printe.</li>
      </ul>
      <p>S ne uitam la un exemplu simplu, dar sugestiv.</p>

      {/* <Button /> */}

      <StateAscension />

      <p>
        Cnd se d click pe buton, starea din App este actualizat folosind o
        funcie callback al crei context este legat de App. Acest pattern
        stabilete o linie clar ntre componentele "inteligente" i "stupide".
      </p>
      <p>
        Pattern-ul de modificare a strii prin ascensiune poate avea orice
        suprapunere.
      </p>
      <img
        src="https://textbook.edu.goit.global/react-zr7b4k/v1/ro/img/lesson-03/deep-state-hoisting.gif"
        alt="Gif"
      />

      <h3>Tipuri de date interne ale unei componente class based</h3>
      <ul>
        <li>
          <b>static data</b> - proprieti i metode statice care trebuie
          accesate fr o instan.
        </li>
        <li>
          <b>this.state.data</b> - modificarea dinamic a datelor prin metodele
          componentei, stare.
        </li>
        <li>
          <b>this.data</b> - date care vor fi diferite pentru fiecare instan.
        </li>
        <li>
          <b>const DATA</b> - constante ale cror date nu se modific i sunt
          aceleai pentru toate instanele.
        </li>
      </ul>
    </div>
  );
}

export default Lesson3;
